import React, {Component} from 'react'
import {connect} from 'react-redux'
import {GoogleLogin} from 'react-google-login';
import './Auth.sass';


class Auth extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLogin: false,
      login: '',
      email: '',
      password: ''
    }
  }
  responseGoogle = (response) => {
    console.log(response);
  }
  onFieldChange = (e) => {
    const {id, value} = e.target;
    this.setState({[id]: value})
  }
  onLoginChange = (e) => {
    e.preventDefault()
    this.setState({
      isLogin: !this.state.isLogin
    })
  }
  onEnter = (e) => {
    e.preventDefault();
    this.props.login();
    console.log(this.state)
  }
  
  render() {
    const {isLogin, login, email, password} = this.state;
    return (
      <div className="jumtron vertical-center">
        <div className="container auth ">
          <div className="row">
            <div className="col-md-6 mx-auto text-center">
              <h1>{isLogin
                  ? "Войти"
                  : "Регистрация"}</h1>
            </div>
          </div>
          <div className="row">
            <div className="col-md-7 mx-auto ">
              <form>
                {isLogin
                  ? <div className="form-group">
                      <label htmlFor="login">Логин</label>
                      <input
                        type="text"
                        className="form-control"
                        id="login"
                        placeholder="Введите логин"
                        value={login}
                        onChange={this.onFieldChange}/></div>
                  : null}

                <div className="form-group">
                  <label htmlFor="email">Email</label>
                  <input
                    type="email"
                    className="form-control"
                    id="email"
                    placeholder="Введите email"
                    value={email}
                    onChange={this.onFieldChange}/>
                </div>
                <div className="form-group">
                  <label htmlFor="password">Пароль</label>
                  <input
                    type="password"
                    className="form-control"
                    id="password"
                    placeholder="Введите пароль"
                    value={password}
                    onChange={this.onFieldChange}/>
                </div>
                <div className="row">
                  <div className="col d-flex justify-content-around">
                  <button className="btn btn-rose mr-2" onClick={this.onLoginChange}>{isLogin
                        ? "Войти в аккаунт"
                        : 'Создать аккаунт'}</button>
                    <button className="btn btn-rose " onClick={this.onEnter}>{isLogin
                        ? "Регистрация"
                        : "Вход"}</button>
                  
                  </div>
                </div>

              </form>
            </div>

          </div>
          <div className="row">
            <div className="col-md-6 mt-3 mx-auto text-center ">
              <p>Или войти с помощью</p>
              <GoogleLogin
                clientId="525029115183-o8vt393ca86jsse46vve4icf5fpsig3e.apps.googleusercontent.com"
                buttonText='Google'
                onSuccess={this.responseGoogle}
                onFailure={this.responseGoogle}
                cookiePolicy={'single_host_origin'}/>
            </div>

          </div>
        </div>
      </div>

    )
  }
}

const mapStateToProps = state => {
  return state
}
const mapDispatchToProps = dispatch => {
  return {}
}
export default connect(mapStateToProps, mapDispatchToProps)(Auth)
