let s = JSON.parse(localStorage.getItem('tasks')) || []
const initialState = s

export default function tasks(state = initialState, action) {
  switch (action.type) {
    case "ADD_TASK":
      localStorage.setItem('tasks',JSON.stringify([
        action.payload, ...state
      ]))
      return [
        action.payload, ...state
      ]
    case "DELETE_TASK":
        localStorage.setItem('tasks',JSON.stringify([...state.filter(task => task.id !== action.payload)]))
      return [...state.filter(task => task.id !== action.payload)]
    case "ADD_COUNT_TASK":
        localStorage.setItem('tasks',JSON.stringify([...state.map(task => {
          if (task.id === action.payload.id) {
            task.did += action.payload.did
          }
          return task
        })]))
      return [...state]
    default:
      return state;
  }
}