import React, {Component} from 'react'
import { connect } from 'react-redux'
import './AddTask.sass';
import {addTask} from '../../actions/TasksActions';
class AddTask extends Component {
    state = {
        colors:[ "#F26968","#3DDAD8","#EFAA52","#765285","#323339","#4C586F"],
        done: 2,
        did: 1,
        name: '',
        color:'#FF6F91'
    }
    onChangeHandler = (e) => {
        let {id, value} = e.target;
        if (id === "did" || id === "done") {
            if (value.length < 10) {
                this.setState({
                    [id]: parseInt(value) * 1
                })
            }
        } else if (id === "name") {
            if (value.length < 255) {
                console.log(value)
                this.setState({[id]: value})
            }
        }
    }
    onChangeSetColor = (e) =>{
        const {value} = e.target;
        this.setState({
            color:value
        })
    }

onClickButton = () => {
    const {done,did,name,color} = this.state;
    if(did>done || name===''){
        return false;
    }
    this.props.addTaskAction({
        done,
        did,
        name,color,
        id:Date().toString(10)
    });
    this.setState({
        name:'',
        done:2,
        did:1
    })
}
render() {
    const {done, did, name,colors} = this.state;
    const colorsTemplate  = colors.map((color,id)=>{
        return(
            <label key={id}>
                <input className = "add-task__color"   name="color" value={color} type="radio" onChange={this.onChangeSetColor}/>
                <div className="color-radio"style={{backgroundColor:color}} ></div>
            </label>
        )
    })
    return (
        <div className="container add-task">
            <div className="row">
                <div className="col-md-6">
                    <h1>Новая задача</h1>
                    <div className="form-group ">
                        <label className="add-task__name-label">
                            <textarea placeholder="Название задачи" className="form-control add-task__name" id="name" value={name} onChange={this.onChangeHandler}></textarea>
                        </label>
                    </div>
                </div>
                <div className="col-md-6 number-fields mt-4">
                    <div className="form-group d-flex flex-column align-items-center">
                        <label className="add-task__number-label">Сделано
                            <input
                                id="did"
                                className="form-control"
                                type="number"
                                value={did}
                                onChange={this.onChangeHandler}/>
                        </label>
                    </div>
                    <div className="form-group d-flex flex-column align-items-center">
                        <label className="add-task__number-label">Цель
                            <input
                                id="done"
                                className="form-control "
                                type="number"
                                value={done}
                                onChange={this.onChangeHandler}/>
                        </label>
                    </div>
                    <div className="form-group d-flex flex-row justify-content-center">
                        {colorsTemplate}
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-6"></div>
                <div className="col-md-6 d-flex flex-column align-items-center">
                    <button onClick={this.onClickButton} className="btn btn-primary">Добавить задачу</button>
                </div>
            </div>
        </div>
    )
}
}
const mapStateToProps = state =>{
    return{state}
}
const mapDispatchToProps = dispatch =>({
    addTaskAction: task =>dispatch(addTask(task))
})
export default connect(mapStateToProps, mapDispatchToProps)(AddTask)