import React, {Component} from 'react'
import {Progress} from 'reactstrap'
import {connect} from 'react-redux'
import './Task.sass';
import {deleteTask} from '../../actions/TasksActions';
import {addCountTask} from '../../actions/TasksActions';
class Task extends Component {
  state = {
    value: 1
  }
  onChangeHandler = (e) => {
    let {id, value} = e.target;

    if (value.length < 10) {
      this.setState({
        [id]: parseInt(value) * 1
      })
    }
  }
  onClickButton = (e) => {
    const {value} = this.state;
    if (this.props.data.done < this.props.data.did + value) {
      return false;
    }
    this.setState({value: 1})
    this
      .props
      .addCountTaskAction({id: this.props.data.id, did: this.state.value})
  }
  onClickDeleteButton = (e) => {
    this
      .props
      .deleteTaskAction(this.props.data.id)
  }
  render() {
    const {value} = this.state;
    const {id, name, did, done, color} = this.props.data;
    const complete = done === did;
    console.log()
    return (
      <div key={id} className="col-md-6 ">
        <div
          className="col-md-12  add-task task"
          style={{
          backgroundColor: !complete
            ? color
            : "#6CBF84"
        }}>
          <div className="row ">
            <div className="col-11 float-right">
              <h3>{name}</h3>
            </div>
            <div className=" col-1 float-left">
              <button
                type="button"
                className="close"
                aria-label="Close"
                onClick={this
                .onClickDeleteButton
                .bind(this)}>
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
          </div>
          <div className="row d-flex justify-content-center">
            <div className="col-md-12 ">
              <div className="text-center mb-2">
                {did}&nbsp;из&nbsp;{done}
              </div>
              {complete
                ? <Progress color="success" value={did} max={done}>{(did * 100 / done).toFixed(1)}%</Progress>
                : <Progress color="info" value={did} max={done}>{(did * 100 / done).toFixed(1)}%</Progress>
}

            </div>
          </div>

          {!complete
            ? <div className="form-froup row mt-3">
                <h5 className="col-lg-3 mt-2 pt-2 align-bottom">
                  Выполнено:</h5>
                <div className="col-lg-5 d-flex justify-content-center mt-2">
                  <input
                    className="form-control "
                    type="number"
                    onChange={this.onChangeHandler}
                    id="value"
                    value={value}/>
                </div >
                <div className="col-lg-4 mt-2 d-flex flex-row-reverse">
                  <button
                    className="btn btn-primary "
                    onClick={this
                    .onClickButton
                    .bind(this)}>Потвердить</button>
                </div>
              </div>
            : <div className="form-froup row mt-3">
              <div className="col text-center">
                <h4>Выполнено</h4>

              </div>
            </div>
}

        </div>

      </div>
    )

  }
}
const mapStateToProps = state => {
  return {state}
}

const mapDispatchToProps = dispatch => ({
  deleteTaskAction: id => dispatch(deleteTask(id)),
  addCountTaskAction: countAndId => dispatch(addCountTask(countAndId))
})
export default connect(mapStateToProps, mapDispatchToProps)(Task)